#root {
  text-align: center;
}
#root table {
  display: inline-block;
}
#root thead {
  background-color: #333333;
  color: rgb(255,255,255);
  font-weight: bold;
}
#root td {
  padding: 5px;
}
#root tbody tr:nth-child(even) {
  background-color: rgb(200,200,200);
}



